<template>
  <div class="page page-user-name">
    <van-field v-model="username" autocomplete="new-password" placeholder="请输入用户名"></van-field>
    <div class="tip">用户名为4-20位字符</div>
  </div>
</template>

<script>
import {editUserName} from "@/apis/user";

export default {
  data() {
    return {
      username: "",
    };
  },
  created() {
    this.username = this.$root.userInfo.username;
    this.$root.$emit("root:set-topbar", ["left-back", "custom-btn"], {
      label: "保存",
      onClick: () => this.save(),
    });
  },
  methods: {
    save() {
      if (this.username.length < 4 || this.username.length > 20)
        return this.$toast.fail("用户名为4-20位字符");
      editUserName({
        userName: this.username,
      }).then(({ok, msg}) => {
        if (ok) {
          this.$root.userInfo.userName = this.username;
          this.$root.userInfo.isUserName = 1;
          this.$toast.success(msg);
          this.$router.back();
        } else {
          this.$toast.fail(msg);
        }
      });
    },
  },
};
</script>

<style lang="scss">
.page-user-name {
  .van-field {
    background-color: #f0f0f0ff;
    margin: 15px 0 10px;
  }

  .tip {
    font-size: 12px;
    color: #999999ff;
    padding-left: 12px;
  }
}
</style>
